



















import { Component, Prop, Vue, Watch } from "vue-property-decorator";

@Component({
  components: {},
})
export default class Draw extends Vue {
  @Prop() resetDraw!: boolean;
  canvas: any = null;
  x: number = 0;
  y: number = 0;
  lineWidth = 10;
  strokeStyle = "#ffc81e";
  isDrawing: boolean = false;

  mounted() {
    var c = document.getElementById("myCanvas") as HTMLCanvasElement;
    if (c != null) {
      this.canvas = c.getContext("2d");
    }
  }
  showCoordinates(e: any) {
    this.x = e.offsetX;
    this.y = e.offsetY;
  }

  keepDrawing(e: any) {
    e.preventDefault();
    if (this.isDrawing == true) {
      const ha = this.getMosuePositionOnCanvas(e);
      this.canvas.strokeStyle = this.strokeStyle;
      this.canvas.lineTo(ha.x, ha.y);
      this.canvas.stroke();
    }
  }
  beginDrawing(e: any) {
    e.preventDefault();
    const ha = this.getMosuePositionOnCanvas(e);
    this.canvas.beginPath();
    this.canvas.moveTo(ha.x, ha.y);
    this.canvas.lineWidth = this.lineWidth;
    this.canvas.fill();
    this.isDrawing = true;
  }
  stopDrawing(e: any) {
    e.preventDefault();

    if (this.isDrawing == true) {
      this.x = 0;
      this.y = 0;
      this.isDrawing = false;
      this.$emit("checkDone");
    }
  }
  clearCanvas() {
    var c = document.getElementById("myCanvas") as HTMLCanvasElement;
    this.canvas.clearRect(0, 0, c.width, c.height);
  }
  getMosuePositionOnCanvas(event: any) {
    const clientX = event.clientX || event.touches[0].clientX;
    const clientY = event.clientY || event.touches[0].clientY;
    const { offsetLeft, offsetTop } = event.target;
    // console.log(offsetLeft);
    const canvasX = clientX - offsetLeft;
    const canvasY = clientY - offsetTop - 60;
    // const clientX = event.clientX - 500 || event.touches[0].clientX + 265;
    // const clientY = event.clientY - 150 || event.touches[0].clientY - 150;
    // const { offsetLeft, offsetTop } = event.target;
    // console.log(clientX, clientY);
    // const canvasX = clientX - offsetLeft - 280;
    // const canvasY = clientY - offsetTop;

    return { x: canvasX, y: canvasY };
  }
  @Watch("resetDraw")
  wResetDraw() {
    this.clearCanvas();
  }
}
