

















































import { Component, Vue, Watch } from "vue-property-decorator";

import { SSR, SR, R } from "@/store/newlist";

import Draw from "@/components/Draw.vue";
import { Action, Getter } from "vuex-class";
@Component({
  components: {
    Draw,
  },
})
export default class Flip extends Vue {
  SSRCard = SSR;
  SRCard = SR;
  RCard = R;
  listSSRHasRecive: any = [];
  overlay_SSR: boolean = false;
  overlay: boolean = false;
  resetDraw: boolean = false;
  hidden: boolean = false;
  @Getter disable1!: boolean;
  @Getter hiddenvideo!: boolean;
  @Action("getData")
  getData(value: any) {
    this.getData(value);
  }
  @Action("checkAmulet")
  checkAmulet(value: number) {
    this.checkAmulet(value);
  }
  card: object = { rank: "", img: "", name: "", video: "" };

  closeAnimation() {
    this.overlay_SSR = false;
  }
  reset() {
    this.overlay = false;
    this.resetDraw = false;
    this.hidden = false;
  }
  checkDone() {
    this.overlay = true;
    setTimeout(() => {
      this.overlay = false;
      this.resetDraw = true;
      this.hidden = true;
      this.callRandomCharacter();

      setTimeout(() => {
        this.resetDraw = false;
      }, 500);
    }, 1500);
  }
  rnd(a: number, b: number) {
    return Math.floor((b - a + 1) * Math.random()) + a;
  }
  getCard(val: any) {
    let num: number = this.rnd(0, val.length - 1);
    let name = val[num];
    if (val[num].prioritized == "SSR") {
      this.listSSRHasRecive.push(name);
    }
    this.card = name;
  }
  callRandomCharacter() {
    this.checkAmulet(1);
    let x = this.rnd(1, 100);
    if (x == 1) {
      this.getCard(this.SSRCard);

      this.overlay_SSR = true;
    }
    if (1 < x && x <= 27) {
      this.getCard(this.SRCard);
    }
    if (28 <= x && x <= 100) {
      this.getCard(this.RCard);
    }
  }
  @Watch("listSSRHasRecive")
  wListSSRHasRecive() {
    this.getData(this.listSSRHasRecive);
  }
}
