



















import { Component, Vue } from "vue-property-decorator";

@Component({
  components: {},
})
export default class Draw extends Vue {
  canvas: any = null;
  x: number = 0;
  y: number = 0;
  lineWidth = 10;
  strokeStyle = "#ffc81e";
  isDrawing: boolean = false;

  mounted() {
    var c = document.getElementById("myCanvas") as HTMLCanvasElement;
    if (c != null) {
      this.canvas = c.getContext("2d");
    }
  }
  showCoordinates(e: any) {
    this.x = e.offsetX;
    this.y = e.offsetY;
  }
  // drawLine(x1: number, y1: number, x2: number, y2: number) {
  //   let ctx = this.canvas;
  //   ctx.beginPath();
  //   ctx.strokeStyle = "#ffc81e";
  //   ctx.lineWidth = 5;
  //   ctx.moveTo(x1, y1);
  //   ctx.lineTo(x2, y2);
  //   ctx.stroke();
  //   ctx.closePath();
  // }
  keepDrawing(e: any) {
    e.preventDefault();
    if (this.isDrawing == true) {
      const ha = this.getMosuePositionOnCanvas(e);
      this.canvas.strokeStyle = this.strokeStyle;
      this.canvas.lineTo(ha.x, ha.y);
      this.canvas.stroke();
    }
  }
  beginDrawing(e: any) {
    e.preventDefault();
    const ha = this.getMosuePositionOnCanvas(e);
    this.canvas.beginPath();
    this.canvas.moveTo(ha.x, ha.y);
    this.canvas.lineWidth = this.lineWidth;
    this.canvas.fill();
    this.isDrawing = true;
  }
  stopDrawing(e: any) {
    e.preventDefault();
    if (this.isDrawing == true) {
      this.x = 0;
      this.y = 0;
      this.isDrawing = false;
    }
  }
  getMosuePositionOnCanvas(event: any) {
    const clientX = event.clientX - 390 || event.touches[0].clientX + 280;
    const clientY = event.clientY - 150 || event.touches[0].clientY - 150;
    const { offsetLeft, offsetTop } = event.target;

    const canvasX = clientX - offsetLeft - 280;
    const canvasY = clientY - offsetTop;

    return { x: canvasX, y: canvasY };
  }
}
