






















































import { Component, Vue, Watch } from "vue-property-decorator";

import { SSR, SR, R } from "@/store/newlist";

import Draw from "@/components/Draw.vue";
import { Action, Getter } from "vuex-class";
@Component({
  components: {
    Draw,
  },
})
export default class Flip extends Vue {
  SSRCard = SSR;
  SRCard = SR;
  RCard = R;
  @Getter disable10!: boolean;

  overlay_SSR: boolean = false;
  overlay: boolean = false;
  resetDraw: boolean = false;
  hidden: boolean = false;
  listVideo: Array<object> = [];
  listSSRHasRecive: any = [];
  cards: Array<object> = [];
  videoLink: any = null;

  countVideo: number = 1;
  @Getter hiddenvideo!: boolean;
  @Action("getData")
  getData(value: any) {
    this.getData(value);
  }
  @Action("checkAmulet")
  checkAmulet(value: number) {
    this.checkAmulet(value);
  }
  closeAnimation() {
    if (this.listVideo.length > this.countVideo) {
      this.videoLink = this.listVideo[this.countVideo];
      this.countVideo++;
    } else {
      this.overlay_SSR = false;
      this.hidden = true;
      this.countVideo = 1;
    }
  }
  reset() {
    this.overlay = false;
    this.resetDraw = false;
    this.hidden = false;
  }
  checkDone() {
    this.overlay = true;

    setTimeout(() => {
      this.hidden = true;
    }, 1700);
    setTimeout(() => {
      this.overlay = false;
      this.resetDraw = true;

      this.callRandomCharacter();

      setTimeout(() => {
        this.resetDraw = false;
      }, 7);
      // this.hidden = true;
    }, 1500);
  }
  rnd(a: number, b: number) {
    return Math.floor((b - a + 1) * Math.random()) + a;
  }
  getCard(val: any, slot: any) {
    let num: number = this.rnd(0, val.length - 1);
    let name = val[num];
    this.cards[slot] = name;
    this.getVideo();
  }
  sort() {
    this.cards.sort(function(a: any, b: any): any {
      if (a.prioritized > b.prioritized) return -1;
    });
  }
  getVideo() {
    this.listVideo = this.cards.map((c: any) => {
      if (c.video != "") {
        return c.video;
      }
    });
    this.listVideo = this.listVideo.filter((b) => {
      return b;
    });
  }
  callRandomCharacter() {
    this.checkAmulet(10);
    for (let i = 0; i < 10; i++) {
      let x = this.rnd(1, 100);
      // console.log(x);
      if (x == 1 || x == 2) {
        this.getCard(this.SSRCard, i);

        this.sort();
        this.videoLink = this.listVideo[0];
        this.overlay_SSR = true;
      }
      if (2 < x && x <= 28) {
        this.getCard(this.SRCard, i);
        this.sort();
      }
      if (29 <= x && x <= 100) {
        this.getCard(this.RCard, i);
        this.sort();
      }
    }

    this.cards.map((c: any) => {
      if (c.prioritized == "SSR") {
        this.listSSRHasRecive.push(c);
      }
    });
    // console.log(this.listVideo);
  }
  @Watch("listSSRHasRecive")
  wListSSRHasRecive() {
    this.getData(this.listSSRHasRecive);
  }
}
